import React, { useEffect, useState } from 'react';
import classnames from 'classnames';
import {
  TipsMessageMap,
} from './constants';
import styles from './index.less';
import { sleep } from '@/utils/tool';

const Tips: React.FC = () => {
  const [tip, setTip] = useState('');
  const [switchTip, setSwitchTip] = useState(false);
  const tipsMessageList: string[] = [...TipsMessageMap.management.list, ...TipsMessageMap.life.list];

  const sayTipsMessage = async () => {
    setSwitchTip(true);
    await sleep(1500);
    setTip(`“${tipsMessageList[Math.random() * tipsMessageList.length >> 0]}”`);
    setSwitchTip(false);
  };

  useEffect(() => {
    const timeId = setTimeout(sayTipsMessage, 10000);
    return () => clearTimeout(timeId);
  }, [tip]);

  useEffect(() => {
    sayTipsMessage();
  }, []);

  return (
    <div className={classnames(styles.tips, switchTip && styles.switchTip)}>{tip}</div>
  );
};

export default Tips;
