import React, { useEffect, useState } from 'react';
import { Row, Col, Avatar } from 'antd';
import {
  AppsAllList, TAppItem
} from './constants';
import styles from './index.less';

const Apps: React.FC = () => {
  const [apps, setApps] = useState<TAppItem[]>([]);

  useEffect(() => {
    setApps(AppsAllList.filter(i => i.backgroundColor !== '#fff'));
  }, []);

  const gotoUrl = (url: string) => { window.open(url); };

  return (
    <div className={styles.apps}>
      <Row gutter={[16, 32]}>
        {apps.map((i, index) => (
          <Col span={2} key={index}>
            <Avatar
              className={styles.appItem}
              shape="square"
              size={64}
              style={{
                backgroundColor: i.backgroundColor || '#c7c7cc',
                color: i.color || '#fff',
              }}
              onClick={() => gotoUrl(i.url)}
            >
              {i.name}
            </Avatar>
          </Col>
        ))}
      </Row>
    </div>
  );
};

export default Apps;
