import React, { useState, useEffect } from 'react';
import { dateFormat } from '@/utils/tool';
import styles from './index.less';

const Time: React.FC = () => {
  const [date, setDate] = useState(new Date());
  useEffect(() => {
    const tick = () => setDate(new Date());
    const timeId = setTimeout(tick, 1000);
    return () => clearTimeout(timeId);
  }, [date]);

  const getWeek = () => {
    const weeks = new Array('日', '一', '二', '三', '四', '五', '六');
    const dayNum = date.getDay();
    return `星期${weeks[dayNum]}`;
  };

  const renderTime = () => {
    return dateFormat(date, 'hh/:/mm').split('/').map((item, index) => {
      if (item === ':') {
        return <span key={index} className={styles.colon}>{item}</span>;
      } else {
        return <time key={index}>{item}</time>;
      }
    });
  };

  return (
    <>
      <div className={styles.time}>{renderTime()}</div>
      <div className={styles.date}>
        <span className={styles.timeMonth}>{dateFormat(date, 'MM月dd日')}</span>
        <span className={styles.timeWeek}>{getWeek()}</span>
      </div>
    </>
  );
};

export default Time;
