import React from 'react';
import { Carousel } from 'antd';
import { dateFormat } from '@/utils/tool';
import styles from './index.less';

const Wallpaper: React.FC = () => {
  const count = 1;
  const days = new Array(count)
    .fill('')
    .map((i, index) => `https://cn.bing.com/iod/1366/1024/${dateFormat(Date.now() - 24*60*60*1000*index, 'yyyyMMdd')}1600`);
  return (
    <div className={styles.wallpaper}>
      <Carousel autoplay effect="fade" dots={false}>
        {days.map((imageUrl, index) => (
          <div key={index}>
            <div className={styles.wallpaperItem} style={{ backgroundImage: `url(${imageUrl})` }} />
          </div>
        ))}
      </Carousel>
    </div>
  );
};

export default Wallpaper;
